import React, {Component} from 'react';

import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Navbar from 'react-bootstrap/Navbar';
import Container from 'react-bootstrap/Container';
import Card from 'react-bootstrap/Card'
import Form from 'react-bootstrap/Form';
import Tab from 'react-bootstrap/Tab'
import Tabs from 'react-bootstrap/Tabs'
import Alert from 'react-bootstrap/Alert';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Button from 'react-bootstrap/Button';
import Spinner from 'react-bootstrap/Spinner';
import Dropdown from 'react-bootstrap/Dropdown'
import {Settings, BarChart2, Info, Sliders, TrendingUp, TrendingDown, Activity} from 'react-feather';

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            getPageListIsLoading: false,
            pageList: {},
            postList: []
        };
        this.addPage = this.addPage.bind(this);
        this.createPage = this.createPage.bind(this);
        this.getPageList = this.getPageList.bind(this);
    }

    createPage() {

    }

    getPageList() {
        this.setState({getPageListIsLoading: true});
        fetch("/api/getPages").then(response => response.json())
            .then( (data) => {
            console.log(data)
            this.setState({pageList: data})
            })
            .catch((error) => {alert(`${error} retrieving pages failed.`)})
            .finally((data) => { this.setState({getPageListIsLoading: false})} )
    }

    addPage() {

    }

    render() {
        return (
            <Container fluid={true}>
                <Navbar bg="light" expand="lg">
                    <Navbar.Brand href="#home">React-Bootstrap</Navbar.Brand>
                </Navbar>
                <br/>
                <Row>
                    <Col xs={4}>
                        <Card>
                            <Card.Header>CRUD Operations on Pages</Card.Header>
                            <Card.Body>
                                <Card.Text>
                                </Card.Text>
                                <Form>
                                    <Form.Group controlId="postForm.Name">
                                        <Form.Label>Page Name</Form.Label>
                                        <Form.Control type="name" placeholder="Example page name...."/>
                                    </Form.Group>
                                    <Form.Group controlId="PostForm.ID">
                                        <Form.Label>Page ID</Form.Label>
                                        <Form.Control as="select">
                                            <option>1</option>
                                            <option>2</option>
                                            <option>3</option>
                                            <option>4</option>
                                            <option>5</option>
                                        </Form.Control>
                                    </Form.Group>
                                    <Form.Group controlId="PostForm.Icon">
                                        <Form.Label>Page Icon</Form.Label>
                                        <Form.Control as="select">
                                            <option>1</option>
                                            <option>2</option>
                                            <option>3</option>
                                            <option>4</option>
                                            <option>5</option>
                                        </Form.Control>
                                    </Form.Group>
                                </Form>
                                <Button variant={'primary'}
                                        disabled={(this.state.isLoading)}
                                        onClick={!(this.state.isLoading) ? this.addPage : null}>
                                    {(this.state.isLoading) ? <Spinner
                                        as="span"
                                        animation="grow"
                                        size="sm"
                                        role="status"
                                        aria-hidden="true"
                                    /> : ''}
                                    {'Add Page'}
                                </Button>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col xs={4}>
                        <Card>
                            <Card.Header>Pages</Card.Header>
                            <Card.Body>
                                <Card.Title>CRUD Operations on Pages</Card.Title>
                                <Card.Text>
                                </Card.Text>
                                <Button variant={'primary'}
                                        disabled={(this.state.getPageListIsLoading)}
                                        onClick={!(this.state.getPageListIsLoading) ? this.getPageList : null}>
                                    {(this.state.getPageListIsLoading) ? <Spinner
                                        as="span"
                                        animation="grow"
                                        size="sm"
                                        role="status"
                                        aria-hidden="true"
                                    /> : ''}
                                    {'Get Page List'}
                                </Button>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>

        );
    }
}

export default App;



